@import '../WebflowScroll/styles.less';

// --- Animations ---

@keyframes gradientShift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes blobMove {
  0% {
    transform: translate(0, 0) scale(1);
  }
  33% {
    transform: translate(30px, -50px) scale(1.1);
  }
  66% {
    transform: translate(-20px, 20px) scale(0.9);
  }
  100% {
    transform: translate(0, 0) scale(1);
  }
}

@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}

// --- Global Styles (Theme Base) ---

:global {
  .welcome-container {
    min-height: 100vh;
    color: #1f1f1f;
    // Theme Background from Welcome_old
    background: radial-gradient(circle at 10% 20%, #e9e3ff, #b5fffc);
    background-size: 200% 200%;
    animation: gradientShift 15s ease infinite;
    overflow-x: hidden;
  }

  // Blob Styles
  .blob {
    position: absolute;
    height: 400px;
    width: 400px;
    border-radius: 50%;
    opacity: 0.6;
    filter: blur(100px);
    mix-blend-mode: screen;
    animation: blobMove 10s infinite ease-in-out;
  }

  .blob1 {
    top: 10%;
    left: 20%;
    background: radial-gradient(circle at center, #ff718b, #ffeced);
    animation-delay: 0s;
  }

  .blob2 {
    top: 40%;
    right: 15%;
    background: radial-gradient(circle at center, #87e6fb, #6f9eff);
    animation-delay: 5s;
  }

  .blob3 {
    bottom: 10%;
    left: 10%;
    background: radial-gradient(circle at center, #b5fffc, #ffdee9);
    animation-delay: 10s;
  }

  // --- Hero Visuals (Restored & Themed) ---
  .hero-visual {
    max-width: 1400px;
    margin: 60px auto 0;
    perspective: 2000px;

    .visual-grid {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 32px;
      padding: 20px;

      .visual-item {
        position: relative;
        cursor: pointer;
        transform-style: preserve-3d;
        transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
        min-height: 320px;

        .visual-item-inner {
          // Glassmorphism
          background: rgba(255, 255, 255, 0.3);
          backdrop-filter: blur(10px);
          border: 1px solid rgba(255, 255, 255, 0.4);
          border-radius: 24px;
          padding: 48px 32px;
          text-align: center;
          height: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          box-shadow: 0 8px 32px rgba(0, 0, 0, 0.05);
          transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
          position: relative;
          overflow: hidden;

          &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: linear-gradient(
              135deg,
              rgba(255, 255, 255, 0) 0%,
              rgba(255, 255, 255, 0.4) 50%,
              rgba(255, 255, 255, 0) 100%
            );
            transform: translateX(-100%);
            transition: transform 0.6s ease;
          }
        }

        &:hover .visual-item-inner {
          border-color: rgba(255, 255, 255, 0.8);
          box-shadow: 0 24px 64px rgba(0, 0, 0, 0.1);
          background: rgba(255, 255, 255, 0.5);

          &::before {
            transform: translateX(100%);
          }
        }

        .visual-icon {
          font-size: 5rem;
          margin-bottom: 24px;
          filter: drop-shadow(0 4px 12px rgba(0, 0, 0, 0.1));
          transition: filter 0.3s ease;
          position: relative;
        }

        &:hover .visual-icon {
          filter: drop-shadow(0 8px 20px rgba(0, 0, 0, 0.2));
        }

        .visual-name {
          font-size: 1.4rem;
          color: #000;
          font-weight: 700;
          line-height: 1.3;
          margin-bottom: 12px;
          position: relative;
        }

        .visual-desc {
          font-size: 0.95rem;
          color: #666;
          line-height: 1.5;
          max-width: 280px;
          position: relative;
        }

        // Featured item styles
        &.featured-left {
          .visual-item-inner {
            background: rgba(255, 245, 245, 0.4);
          }
        }

        &.featured-center {
          .visual-item-inner {
            border-width: 2px;
            border-color: rgba(255, 255, 255, 0.6);
          }

          &:hover .visual-item-inner {
            box-shadow: 0 30px 80px rgba(0, 0, 0, 0.15);
          }
        }

        &.featured-right {
          .visual-item-inner {
            background: rgba(240, 253, 244, 0.4);
          }
        }
      }
    }
  }

  // --- Feature Sections (Restored & Themed) ---
  .feature-section {
    max-width: 1280px;
    margin: 0 auto;
    padding: 120px 6vw;
    position: relative;

    &.feature-hero {
      // Glassmorphism for "Build amazing things" section
      background: rgba(255, 255, 255, 0.25);
      backdrop-filter: blur(10px);
      border-radius: 30px;
      margin-bottom: 40px;
      border: 1px solid rgba(255, 255, 255, 0.3);
      box-shadow: 0 20px 40px rgba(0, 0, 0, 0.05);

      &::before {
        content: none;
      }
    }

    &.reverse {
      .feature-content {
        flex-direction: row-reverse;
      }
    }

    .feature-content {
      display: flex;
      align-items: center;
      gap: 80px;

      @media (max-width: 968px) {
        flex-direction: column !important;
        gap: 48px;
      }
    }

    .feature-text-block {
      flex: 1;
      max-width: 560px;

      .feature-eyebrow {
        display: inline-block;
        font-size: 0.9rem;
        font-weight: 600;
        text-transform: uppercase;
        letter-spacing: 1px;
        color: #666;
        margin-bottom: 16px;
      }

      .feature-title {
        font-size: clamp(2rem, 4vw, 3rem);
        font-weight: 800;
        line-height: 1.2;
        letter-spacing: -1px;
        margin-bottom: 24px;
        color: #000;
      }

      .feature-description {
        font-size: 1.2rem;
        color: rgba(0, 0, 0, 0.7);
        line-height: 1.7;
        margin-bottom: 32px;
      }

      .feature-list {
        list-style: none;
        padding: 0;
        margin: 0 0 32px 0;

        li {
          position: relative;
          padding-left: 32px;
          margin-bottom: 16px;
          font-size: 1.05rem;
          color: rgba(0, 0, 0, 0.75);
          line-height: 1.6;

          &::before {
            content: 'âœ“';
            position: absolute;
            left: 0;
            top: 0;
            font-weight: bold;
            color: #000;
            font-size: 1.2rem;
          }
        }
      }

      .inline-testimonial {
        background: rgba(255, 255, 255, 0.4);
        border-left: 4px solid #000;
        padding: 24px;
        margin: 32px 0;
        border-radius: 8px;

        blockquote {
          font-size: 1.05rem;
          font-style: italic;
          color: #333;
          margin: 0 0 16px 0;
          line-height: 1.6;
        }

        .testimonial-author {
          display: flex;
          flex-direction: column;
          gap: 4px;

          strong {
            font-weight: 600;
            color: #000;
          }

          span {
            font-size: 0.9rem;
            color: #666;
          }
        }
      }
    }

    .feature-visual {
      flex: 1;
      max-width: 600px;

      .visual-grid-section {
        .tool-grid {
          display: grid;
          grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
          gap: 24px;
        }

        .tool-grid-stacked {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          gap: 24px;
          perspective: 1000px;
        }

        .tool-card {
          background: rgba(255, 255, 255, 0.4);
          border: 1px solid rgba(255, 255, 255, 0.3);
          border-radius: 18px;
          padding: 32px 24px;
          text-align: center;
          cursor: pointer;
          box-shadow: 0 6px 20px rgba(0, 0, 0, 0.08);
          transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
          position: relative;
          backdrop-filter: blur(10px);

          &:hover {
            border-color: rgba(255, 255, 255, 0.8);
            box-shadow: 0 16px 48px rgba(0, 0, 0, 0.18);
            transform: translateY(-12px) scale(1.02);
            background: rgba(255, 255, 255, 0.6);
          }

          .tool-icon {
            font-size: 3rem;
            margin-bottom: 16px;
            filter: grayscale(0.2);
            transition: filter 0.3s ease;
          }

          &:hover .tool-icon {
            filter: grayscale(0);
          }

          .tool-name {
            font-size: 1.05rem;
            font-weight: 600;
            margin: 0 0 10px 0;
            color: #000;
          }

          .tool-desc {
            font-size: 0.9rem;
            color: #666;
            margin: 0;
            line-height: 1.5;
          }
        }
      }

      .visual-showcase {
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 0;
        perspective: 1200px;

        &.stacked {
          .showcase-card {
            position: relative;
            margin-bottom: -140px;

            &:last-child {
              margin-bottom: 0;
            }

            &:nth-child(odd) {
              transform: rotate(-2deg);
              margin-left: -20px;
            }

            &:nth-child(even) {
              transform: rotate(2deg);
              margin-right: -20px;
            }

            &:hover {
              transform: rotate(0deg) !important;
              z-index: 100 !important;
            }
          }
        }

        .showcase-card {
          background: rgba(255, 255, 255, 0.4);
          border: 1px solid rgba(255, 255, 255, 0.3);
          border-radius: 20px;
          padding: 40px 32px;
          text-align: center;
          cursor: pointer;
          box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
          transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
          backdrop-filter: blur(10px);

          &:hover {
            border-color: rgba(255, 255, 255, 0.8);
            box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
            transform: scale(1.05) translateZ(20px);
            background: rgba(255, 255, 255, 0.6);
          }

          .showcase-icon {
            font-size: 4.5rem;
            margin-bottom: 24px;
            filter: grayscale(0.2);
            transition: filter 0.3s ease;
          }

          &:hover .showcase-icon {
            filter: grayscale(0);
          }

          .showcase-name {
            font-size: 1.2rem;
            font-weight: 600;
            color: #000;
          }
        }
      }
    }
  }
}

// --- Local Scoped Styles ---

.container {
  // Transparent to let global background show
  background: transparent;
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
}

.blobContainer {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
}

.hero {
  min-height: 100vh;
  padding-top: 100px;
  background: transparent;

  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.heroContent {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;

  position: relative;
  padding: 0 20px;
}

.heroTitle {
  font-weight: 800;
  font-size: clamp(3rem, 6vw, 5rem);
  line-height: 1.1;
  letter-spacing: -2px;
  margin-bottom: 24px;

  // Gradient Text Theme
  background: linear-gradient(90deg, #ff718b, #41b3ff);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.heroSubtitle {
  font-size: clamp(1.2rem, 2vw, 1.5rem);
  color: rgba(0, 0, 0, 0.65);
  max-width: 720px;
  margin: 0 auto 40px;
  line-height: 1.6;
}

.heroCta {
  margin-bottom: 60px;
  display: flex;
  justify-content: center;
}

// --- Feature Sections (Glassmorphism) ---

.categoryShowcaseSection {
  background: transparent;
}

.featureTextBlock {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  opacity: 0.3;
  transition: opacity 0.5s ease;

  &.active {
    opacity: 1;
  }

  h2 {
    font-size: 3rem;
    margin-bottom: 20px;
    background: linear-gradient(90deg, #ff718b, #41b3ff);
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  p {
    font-size: 1.2rem;
    line-height: 1.6;
    color: #444;
  }
}

.featureImages {
  width: 50%;
  height: 100vh;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.imageWrapper {
  position: absolute;
  width: 80%;
  height: 60%;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

// --- Sticky Scroll Section Overrides ---

.quickAccessSection {
  padding: 0 20px;
  max-width: 800px;
  margin: 0 auto;
  padding-bottom: 20vh;
  position: relative;

  background: transparent; // Override WebflowScroll background
}

.cardContainer {
  position: sticky;
  height: 400px;
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
}

.card {
  width: 100%;
  height: 100%;
  border-radius: 24px;
  padding: 40px;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  transform-origin: center top;
  border: 1px solid rgba(255, 255, 255, 0.2);

  h2 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    color: white;
  }

  p {
    font-size: 1.2rem;
    opacity: 0.9;
  }
}

// --- Other Components ---

.toolsMarqueeSection {
  width: 100%;
  padding: 80px 0;
  background: transparent;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 48px;
  mask-image: linear-gradient(to right, transparent, black 10%, black 90%, transparent);
  -webkit-mask-image: linear-gradient(to right, transparent, black 10%, black 90%, transparent);

  h2 {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 40px;
    background: linear-gradient(135deg, #fff 0%, #a5a5a5 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.marqueeRow {
  display: flex;
  width: 100%;
  margin-bottom: 48px;
  overflow: hidden;
  position: relative;
  -webkit-mask-image: linear-gradient(to right, transparent, black 10%, black 90%, transparent);
  mask-image: linear-gradient(to right, transparent, black 10%, black 90%, transparent);
}

.marqueeTrack {
  display: flex;
  gap: 48px;
  padding: 0 24px;
  width: max-content;
}

.marqueeCard {
  flex: 0 0 auto;
  width: 280px;
  height: 180px;
  border-radius: 16px;
  overflow: hidden;
  position: relative;
  background: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  transition: transform 0.3s ease;
  cursor: pointer;

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
    background: rgba(255, 255, 255, 0.6);
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .cardContent {
    padding: 24px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: left;

    h3 {
      font-size: 1.2rem;
      margin-bottom: 8px;
      font-weight: 600;
    }

    p {
      font-size: 0.9rem;
      color: #666;
    }

    .btn {
      align-self: flex-start;
      padding: 8px 16px;
      background: #000;
      color: #fff;
      border-radius: 20px;
      border: none;
      font-size: 0.85rem;
      cursor: pointer;
    }
  }
}

.catButtonWrapper {
  position: relative;
  display: inline-block;
  cursor: pointer;
  z-index: 10;

  &:hover .catBtn {
    transform: translateY(2px);
    box-shadow: 0 2px 0 #000;
  }
}

.catPaw {
  position: absolute;
  top: -30px;
  right: 20px;
  width: 40px;
  height: 40px;
  z-index: -1;
  pointer-events: none;

  svg {
    width: 100%;
    height: 100%;
  }
}

.catBtn {
  position: relative;
  background: #000;
  color: #fff;
  font-size: 1.2rem;
  font-weight: 700;
  padding: 16px 48px;
  border-radius: 12px;
  border: 2px solid #000;
  box-shadow: 0 6px 0 #000;
  transition: all 0.1s ease;
  cursor: pointer;

  &:active {
    transform: translateY(6px);
    box-shadow: 0 0 0 #000;
  }
}

.footer {
  height: auto;
  background: #000;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;

  border-radius: 40px 40px 0 0;

  h2 {
    font-size: 4rem;
    margin-bottom: 40px;
    color: #fff;
  }
}

.featureHighlightSection {
  position: relative;
  min-height: 300vh;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(20px);
  display: flex;
  justify-content: center;
  margin-top: 100px;
  border-radius: 40px 40px 0 0;
}

.featureContent {
  width: 50%;
  padding: 100px 5%;
}

.ant-pro-layout .ant-pro-layout-content {
  padding-block: 0px;
  padding-inline: 0px;
